.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  transition: color 0.4s ease;

  body.purple & {
    color: $c-purple;
  }

  body.blue & {
    color: $c-blue;
  }

  body.blue-light & {
    color: $c-blue-light;
  }

  body.purple-dark & {
    color: $c-purple-dark;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
    transform: translate(-50%, -50%) scale(0);
    transition:
      transform 0.4s $bounce-effect,
      width 0.4s ease,
      height 0.4s ease,
      background-color 0.3s ease,
      border-color 0.3s ease;
    transition-delay: 0.1s;
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    body.purple & {
      color: $c-purple-dark;
    }

    body.blue & {
      color: $c-blue-light;
    }

    body.blue-light & {
      color: $c-blue;
    }

    body.purple-dark & {
      color: $c-purple;
    }

    &::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    body.purple & {
      color: $c-purple-dark;
    }

    body.blue & {
      color: $c-blue-light;
    }

    body.blue-light & {
      color: $c-blue;
    }

    body.purple-dark & {
      color: $c-purple;
    }

    &::before {
      body.purple & {
        border-color: $c-purple-dark;
      }

      body.blue & {
        border-color: $c-blue-light;
      }

      body.blue-light & {
        border-color: $c-blue;
      }

      body.purple-dark & {
        border-color: $c-purple;
      }
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;

      .social-block__link {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  &:active,
  &:active:focus {
    body & {
      color: $c-light;
    }

    &::before {
      width: 100%;
      height: 100%;
      body.purple & {
        background-color: $c-purple-dark;
        border-color: $c-purple-dark;
      }

      body.blue & {
        background-color: $c-blue-light;
        border-color: $c-blue-light;
      }

      body.blue-light & {
        background-color: $c-blue;
        border-color: $c-blue;
      }

      body.purple-dark & {
        background-color: $c-purple;
        border-color: $c-purple;
      }
    }
  }

  body.loaded & {
    &::before {
      transform: translate(-50%, -50%) scale(1);
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.5s ease;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;

    .social-block__link {
      opacity: 1;
      transform: translateY(0);
    }
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  opacity: 0;
  transform: translateY(0.7rem);
  transition: opacity 0.4s ease, transform 0.4s ease, color 0.3s ease,
    background-color 0.3s ease, border-color 0.3s ease;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-purple-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-purple-dark;
  }

  &:hover {
    body.purple & {
      color: $c-purple-dark;
    }

    body.blue & {
      color: $c-blue-light;
    }

    body.blue-light & {
      color: $c-blue;
    }

    body.purple-dark & {
      color: $c-purple;
    }
  }

  &:focus {
    background-color: $c-light;
    outline: none;

    body.purple & {
      color: $c-purple-dark;
      border-color: $c-purple-dark;
    }

    body.blue & {
      color: $c-blue-light;
      border-color: $c-blue-light;
    }

    body.blue-light & {
      color: $c-blue;
      border-color: $c-blue;
    }

    body.purple-dark & {
      color: $c-purple;
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    body & {
      border-color: $c-light;
    }
    background-color: $c-light;

    body.purple & {
      color: $c-purple-dark;
    }

    body.blue & {
      color: $c-blue-light;
    }

    body.blue-light & {
      color: $c-blue;
    }

    body.purple-dark & {
      color: $c-purple;
    }

  }

  svg {
    @include center;
  }

  &--fb {
    transition-delay: 0.2s, 0.2s, 0s, 0s, 0s;
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    transition-delay: 0.3s, 0.3s, 0s, 0s, 0s;
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    transition-delay: 0.4s, 0.4s, 0s, 0s, 0s;
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
